@charset "UTF-8";
:root {
  --font-body-color: #fff;
  --font-color-second: #4d5bce;
  --font-color-title: #88c0ff;
  --font-color-active: #7796f3;
  --font-color-not-active: #8bb0ff6c;
  --background-color: #0e0e0e;
  --background-second: #020a10;
  --background-selected: #2b2d4156;
  --background-terminal: #16161e;
  --background-side-bar: #191b27;
  --background-active-bar: #1e202e;
  --background-color-title: #0e0e13;
  --background-transparent: #0e0e13da;
  --selected-color: #71cbff;
  --themes-color: #5e4da8;
  --background-thumb: #16161e;
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: #3c548d49;
  --color-active: rgb(4, 217, 255);
  --pos: left center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
