}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.pagination, .services-slide__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet, .services-slide__pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  transition: background 0.5s ease-in-out;
  border-radius: 50%;
  background: #2a3514;
}
.pagination .swiper-pagination-bullet:not(:last-child), .services-slide__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active, .services-slide__pagination .swiper-pagination-bullet-active {
  width: 12px;
  height: 12px;
  content: "";
  transition: scale 0.5s ease-in-out;
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.gradient-black {
  background: linear-gradient(-45deg, #20193c, #020101 70%);
}

.gradient-default {
  background: linear-gradient(to right, #0b0813 10%, #2b2644 50%, #0b0813 90%);
}

.gradient-spec, .offer-container__column {
  background: linear-gradient(-45deg, #fe59c2, #170e31 83%);
  background-size: 200% 200%;
}

.liner-gradient-box {
  background: linear-gradient(45deg, #10133a 55%, rgba(71, 32, 138, 0.4509803922) 10%, #10133a);
}

.text-gradient {
  background: currentColor 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-header {
  background: linear-gradient(-45deg, #1a1530, #332764 60%, rgb(42, 53, 20) 10%, #000 70%);
}

.gradient-services, .audio-player__timeline, body {
  background: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.gradient-title {
  background: linear-gradient(to top, #170e31 10%, #402e6b 50%, #1a1530 100%);
}

.gradient-spec-active {
  background: linear-gradient(-45deg, #04d9ff, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-spec-comfort, .offer-container__column:nth-child(2) {
  background: linear-gradient(-45deg, #c8ff00, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-border-right {
  background: linear-gradient(to right, rgba(42, 53, 20, 0.685) 20%, #332764 50%, #2a3514 80%);
}

.gradient-border-left {
  background: linear-gradient(to left top, #8164ff 40%, rgb(42, 53, 20) 70%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.shadow-after, .services__title:nth-child(1)::after, .offer-container__title::after {
  z-index: -1;
  transform: rotateX(15deg) translateZ(-10px);
  color: rgba(23, 14, 49, 0.479);
  text-shadow: 5px 0 5px #0b0813, 0 0 40px rgba(125, 248, 255, 0.26), 0 0 20px rgba(204, 255, 0, 0.267);
}

.overflow-hidden {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.overflow-hidden::-webkit-scrollbar {
  height: 0;
}

.border-present, .offer-container__thumbnail::before {
  position: absolute;
  display: block;
  color: rgb(4, 217, 255);
  background-image: linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%);
  background-repeat: no-repeat;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  inset: 0.7rem;
}

.link_underline::before, .link_underline::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: rgb(4, 217, 255);
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before {
  width: 45%;
}
.link_underline._active::after {
  width: 45%;
}
.link_underline:hover {
  transition: color 0.4s ease-in-out;
  color: rgb(4, 217, 255);
}
.link_underline:hover::before {
  width: 45%;
}
.link_underline:hover::after {
  width: 45%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.gradient-border-bottom {
  background: linear-gradient(to left top, #8164ff 40%, rgb(42, 53, 20) 70%);
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Manrope", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
