.audio-player {
  height: 3em;
  width: 100%;
  font-family: roboto, sans-serif;
  font-size: 0.75em;
  overflow: hidden;
  display: grid;
  align-content: space-between;
  color: #dfdfdf;
  grid-template-rows: 3rem 6px;
}
.audio-player__timeline {
  background: transparent;
  width: 100%;
  position: relative;
  cursor: pointer;
}
.audio-player__progress {
  background: #1c153f;
  width: 0%;
  height: 100%;
  transition: 0.25s;
}
.audio-player__controls {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 0 20px;
  height: 3rem;
  background: rgba(28, 21, 63, 0.274);
}
.audio-player__play-container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #dfdfdf;
}
.audio-player__toggle-play.play {
  cursor: pointer;
  position: relative;
  left: 0;
  height: 0;
  width: 0;
  border: 7px solid rgba(0, 0, 0, 0);
  border-left: 13px solid #dfdfdf;
}
.audio-player__toggle-play.pause {
  height: 15px;
  width: 20px;
  cursor: pointer;
  position: relative;
}
.audio-player__toggle-play.pause::before {
  position: absolute;
  top: 0;
  left: 0px;
  background: #dfdfdf;
  content: "";
  height: 15px;
  width: 3px;
}
.audio-player__toggle-play.pause::after {
  position: absolute;
  top: 0;
  right: 8px;
  background: #dfdfdf;
  content: "";
  height: 15px;
  width: 3px;
}
.audio-player__time {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}
.audio-player__current {
  padding: 2px;
}
.audio-player__divider {
  padding: 2px;
}
.audio-player__length {
  padding: 2px;
}
.audio-player__name {
  display: flex;
  justify-content: center;
  align-items: center;
}
.audio-player__volume-container {
  cursor: pointer;
  position: relative;
  z-index: 2;
}
.audio-player__volume-container:hover .audio-player__volume-slider {
  left: -123px;
  width: 120px;
}
.audio-player__volume-button {
  height: 26px;
  display: flex;
  align-items: center;
}
.audio-player__volume {
  transform: scale(0.7);
}
.audio-player__volume .icono-volumeHigh,
.audio-player__volume .icono-volumeMedium {
  margin: 4px 16px 4px 5px;
}
.audio-player__volume .icono-volumeHigh:before,
.audio-player__volume .icono-volumeMedium:before {
  border-style: double;
  border-width: 6px;
  left: -2px;
}
.audio-player__volume-slider {
  position: absolute;
  left: -3px;
  top: 15px;
  z-index: -1;
  width: 0;
  height: 15px;
  background: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6666666667);
  transition: 0.25s;
}
.audio-player__volume-percentage {
  background: coral;
  height: 100%;
  width: 75%;
}

